import React, { useState, useEffect } from 'react'
import styled from 'styled-components'

import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import { desktopVw } from '../styles/utils'

import useFetch from '../hooks/useFetch'

import SubscribeForm from './SubscribeForm'

const StyledSubscribeForm = styled(SubscribeForm)`
  background-color: ${props => props.theme.colors.primary};
  .subscribeform {
    &__form {
      margin-top: 0;
    }
    &__input,
    &__submit {
      height: 26px;
      border-color: ${props => props.theme.colors.secondary};
      color: ${props => props.theme.colors.secondary};
      padding: 0 5px;
      font-size: 9px;
      line-height: normal;
      &::placeholder {
        color: ${props => props.theme.colors.secondary};
      }
    }
    &__success-msg {
      color: ${props => props.theme.colors.secondary};
    }
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    .subscribeform {
      &__input,
      &__submit {
        height: ${desktopVw(32)};
        padding: 0 ${desktopVw(5)};
        font-size: ${desktopVw(10)};
        line-height: ${desktopVw(25)};
      }
    }
  }
`

const SuccessMsg = styled.div`
  font-size: 9px;
  line-height: 26px;
  font-weight: 400;
  white-space: pre-wrap;
  background: black;
  color: white;
  text-align: center;
  box-sizing: border-box;
  p {
    margin-bottom: 0 !important;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    height: ${desktopVw(32)};
    padding: ${desktopVw(3)} 0;
    font-size: ${desktopVw(11)};
    line-height: ${desktopVw(25)};
    box-sizing: border-box;
  }
`

const ComingSoonForm = ({ klaviyoListId, successMessage }) => {
  const url = process.env.GATSBY_LASTBRAND_LIST_ENDPOINT

  const { fetchData, response, error, loading } = useFetch()

  const [errorMsg, setErrorMsg] = useState('')

  const [showSuccessMsg, setShowSuccessMsg] = useState(false)
  const [email, setEmail] = useState('')

  const body = {
    klaviyoListId,
    email,
  }

  const options = {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify(body),
  }

  const handleSubmit = e => {
    e.preventDefault()
    setErrorMsg('')
    setShowSuccessMsg(false)

    fetchData(url, options)
  }

  useEffect(() => {
    if (error) {
      console.log(error)
      setErrorMsg('Oops something went wrong try again later')

      return
    }

    if (!response) return

    if (response.detail) {
      setErrorMsg(response.detail)

      return
    }

    setShowSuccessMsg(true)
  }, [response, error])

  const onFocus = () => {
    setErrorMsg('')
  }
  return (
    <>
      {!showSuccessMsg ? (
        <StyledSubscribeForm
          email={email}
          handleChange={setEmail}
          onFocus={onFocus}
          handleSubmit={handleSubmit}
          errorMsg={errorMsg}
          placeholder='Your email'
        />
      ) : (
        <SuccessMsg className='subscribeform__success-msg'>
          {documentToReactComponents(successMessage)}
        </SuccessMsg>
      )}
    </>
  )
}

export default ComingSoonForm
