import React from 'react'
import styled from 'styled-components'

import { desktopVw } from '../styles/utils'

const StyledTag = styled.span`
  padding: 6px 8px;
  background: ${props => props.theme.colors.secondary};
  border-radius: 16px;
  text-transform: uppercase;
  font-size: 6px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: ${desktopVw(6)} ${desktopVw(9)};
    border-radius: ${desktopVw(16)};
    font-size: ${desktopVw(8)};
  }
`
const Tag = ({ children, className = '' }) => {
  return <StyledTag className={className}>{children}</StyledTag>
}

export default Tag
