const ComingSoonThumbnail = ({
  cardData: {
    product = {},
    title: { title },
    price,
    button,
    subscribeForm: { klaviyoListId, successMessage },
    text,
  },
  initialRating,
  smallerCard = false,
}) => {
  const {
    seoTitle = '',
    id = '',
    colors: colorsProp = [],
    tags = [],
    productImages = [],
  } = product || {}
  const colors = colorsProp.filter(
    cObj =>
      productImages.findIndex(imageObj => imageObj.color.name === cObj.name) >
      -1,
  )

  const getImageIdToColors = () => {
    let imageColors = {}
    productImages.forEach(productImage => {
      const { images } = productImage
      const imageColor = productImage.color
      if (images) {
        images.forEach(image => {
          imageColors = { ...imageColors, [image.id]: imageColor }
        })
      }
    })
    return imageColors
  }

  const imageIdToColors = productImages ? getImageIdToColors() : {}

  const [selectedColor, setSelectedColor] = useState(
    colors && colors[0] && colors[0].name,
  )

  const [image, setImage] = useState(
    productImages && productImages[0].images[0],
  )

  const { devices } = useDetectDevices()

  useEffect(() => {
    if (!productImages || !selectedColor) return
    const newImage = productImages.find(
      ({ color: productColor }) => productColor.name === selectedColor,
    )

    if (newImage) {
      setImage(newImage.images[0])
    }
  }, [selectedColor])

  const imageAltName = color => {
    const mainTitle = title || seoTitle
    return `${mainTitle} - ${color}`
  }

  const handleColorSelect = name => {
    setSelectedColor(name)
  }

  const [showInput, setShowInput] = useState(false)

  const handleClick = () => {
    setShowInput(true)
  }

  const rate = value => {
    eventTracker.track(EventType.ENTER_FEEDBACK, {
      action: 'coming_soon',
      entity_type: 'rating',
      value,
      context: {
        id,
        title,
        colors: selectedColor,
      },
    })
    const currState = loadState('coming_soon_rating') || {}
    const newState = { ...currState, [id]: value }
    saveToStorage(newState)
  }

  return (
    <ProductItem>
      <ImageWrapper className='comingsoon-thumbnail__image-wrapper'>
        <StyledImage
          style={{ height: '100%', width: '100%' }}
          fluid={getImageFluid(devices.mobile, image)}
          alt={
            image.description ||
            (imageIdToColors[image.id]
              ? imageAltName(imageIdToColors[image.id].name)
              : image.title)
          }
        />
        <TagsWrap>
          {tags && (
            <StyledTags>
              <Tag className='coming__soon_tag_month'>{tags?.[0]}</Tag>
            </StyledTags>
          )}
        </TagsWrap>
      </ImageWrapper>
      <InfoWrapper>
        <TitleWrapper>
          <ProductTitle smallerCard={smallerCard}>{title}</ProductTitle>
          {price && (
            <Price
              smallerCard={smallerCard}
              dangerouslySetInnerHTML={{
                __html: `$${getStyledPrice(price)}`,
              }}
            />
          )}
        </TitleWrapper>
        <DescWrapper>
          <LeftDescWrapper smallerCard={smallerCard}>
            {!showInput ? (
              <StyledButton onClick={handleClick} black>
                {button?.text}
              </StyledButton>
            ) : (
              <ComingSoonForm
                klaviyoListId={klaviyoListId}
                successMessage={successMessage.json}
              />
            )}
            <Options
              className='product-thumbnail__options'
              smallerCard={smallerCard}
            >
              {colors && (
                <Colors className='product-thumbnail__options-colors'>
                  {colors &&
                    colors.map(({ name, addBorder, hexcode, texture }, i) => (
                      <ColorWrapper
                        key={`${name}${i}`}
                        onClick={() => {
                          handleColorSelect(name)
                        }}
                      >
                        <StyledColorOption
                          color={hexcode}
                          addBorder={addBorder}
                          texture={texture}
                          name={name}
                          onClick={() => {
                            handleColorSelect(name)
                          }}
                          selected={selectedColor === name}
                          showPointer
                        />
                        {selectedColor === name && (
                          <ColorText>{name}</ColorText>
                        )}
                      </ColorWrapper>
                    ))}
                </Colors>
              )}
            </Options>
          </LeftDescWrapper>

          <RightDescWrapper smallerCard={smallerCard}>
            <ProdDesc smallerCard={smallerCard}>
              {documentToReactComponents(text?.json)}
            </ProdDesc>
            <RatingWrapper smallerCard={smallerCard}>
              <RatingText>Rate this product</RatingText>
              <StarContainer>
                <StarRating
                  onClick={rate}
                  initialValue={initialRating ? parseInt(initialRating) : 0}
                />
              </StarContainer>
            </RatingWrapper>
          </RightDescWrapper>
        </DescWrapper>
      </InfoWrapper>
    </ProductItem>
  )
}

export default ComingSoonThumbnail
