import React, { useMemo, useState, useEffect } from 'react'
import { graphql } from 'gatsby'

// Components
import { documentToReactComponents } from '@contentful/rich-text-react-renderer'
import styled from 'styled-components'
import SEO from '../components/SEO'
import ComingSoonThumbnail from '../components/ComingSoonThumbnail'

// Utils
import { desktopVw, mobileVw } from '../styles/utils'
import { loadState } from '../utils/localStorage'
import useDetectDevices from '../hooks/useDetectDevices'
// Styles

const comingSoonRating = loadState('coming_soon_rating') || {}

const StyledSection = styled.section``

const TitleWrapper = styled.div`
  h1 {
    line-height: 1;
    padding-top: 40px;
    padding-bottom: 20px;
    font-size: 48px;
    font-family: ${props => props.theme.fonts.secondary};
    text-align: center;
    text-transform: uppercase;
    white-space: pre-wrap;
    position: relative;
    z-index: 1;
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    h1 {
      font-size: ${desktopVw(50)};
      line-height: ${desktopVw(73)};
      padding-top: 0;
      padding-bottom: 0;
      b {
        letter-spacing: ${desktopVw(-53)};
      }
    }
  }
`

const ProductContainer = styled.div`
  box-sizing: border-box;
  position: relative;
  padding: 0 5px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: 0;
    margin: 0 0 ${desktopVw(80)};
  }
`

const Row = styled.div`
  display: flex;
  flex-flow: wrap;
  width: 100%;
  margin-bottom: 22px;
  justify-content: center;
  justify-content: start;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    justify-content: center;
    margin-bottom: 45px;
  }
`

const RepeatingProductItem = styled.div`
  width: calc(50% - 10px);
  overflow: hidden;
  padding: 0 5px;
  .comingsoon-thumbnail__image-wrapper {
    height: ${mobileVw(212)};
  }

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    width: ${props => (props.smaller ? desktopVw(329) : desktopVw(405))};
    padding: 0;
    margin-right: ${props => (props.smaller ? desktopVw(46) : desktopVw(64))};
    &:last-of-type {
      margin-right: 0px;
    }
    .comingsoon-thumbnail__image-wrapper {
      height: ${desktopVw(404)};
    }
  }
`

const getConfig = (totalProducts, devices) => {
  let config = []
  if (devices.mobile) {
    const noOfRows = parseInt(totalProducts / 2)
    config = Array(noOfRows).fill(2)
    if (totalProducts % 2 == 1) {
      config.push(1)
    }
    return config
  } else {
    if (totalProducts === 1) {
      config.push(1)
    } else if (totalProducts % 3 === 0) {
      const configLength = totalProducts / 3
      config = Array(configLength).fill(3)
    } else if ((totalProducts - 1) % 3 === 0) {
      const configLength = (totalProducts - 1) / 3
      config = Array(configLength).fill(3)
      config[0] = 2
      config.push(2)
    } else {
      const configLength = (totalProducts + 1) / 3
      config = Array(configLength).fill(3)
      config[0] = 2
    }
    return config
  }
}

const getProducts = (productCards, config) => {
  const prod = []
  let index = 0
  config.forEach(item => {
    const chunk = []
    for (let i = 0; i < item; i++) {
      chunk.push(productCards[index])
      index++
    }
    prod.push(chunk)
  })
  return prod
}

const ComingSoon = ({ data }) => {
  const page = data.contentfulComingSoon

  const { devices } = useDetectDevices()

  const totalProducts = page.productCards.length

  const config = useMemo(() => getConfig(totalProducts, devices), [
    devices.mobile,
    devices.desktop,
  ])

  const productList = useMemo(() => getProducts(page.productCards, config), [
    config,
  ])

  const [comingSoonRatingProdIdMap, setComingSoonRatingProdIdMap] = useState({})
  useEffect(() => {
    setComingSoonRatingProdIdMap(comingSoonRating)
  }, [])
  return (
    <>
      <SEO
        title={page.seoTitle}
        description={page.seoDescription}
        type={page.pageType}
        slug={page.slug}
        image={page.metaImage ? page.metaImage.fluid.src : null}
      />
      <StyledSection>
        <TitleWrapper>
          {documentToReactComponents(page.title.json)}
        </TitleWrapper>
        <ProductContainer>
          {productList.map(it => {
            return (
              <Row>
                {it
                  .filter(i => i?.product)
                  .map((p, i) => {
                    return (
                      <RepeatingProductItem smaller={it.length > 2}>
                        <ComingSoonThumbnail
                          key={p?.product?.id}
                          cardData={p}
                          initialRating={
                            comingSoonRatingProdIdMap[p?.product?.id]
                          }
                          smallerCard={it.length > 2}
                        />
                      </RepeatingProductItem>
                    )
                  })}
              </Row>
            )
          })}
        </ProductContainer>
      </StyledSection>
    </>
  )
}

export default ComingSoon

export const ComingSoonQuery = graphql`
  query ComingSoon($id: String!) {
    contentfulComingSoon(id: { eq: $id }) {
      id
      seoTitle
      seoDescription
      metaImage {
        fluid(maxWidth: 1200) {
          ...GatsbyContentfulFluid_withWebp
        }
      }
      title {
        json
      }
      slug
      productCards {
        id
        title {
          title
        }
        price
        image {
          fluid(maxWidth: 960) {
            ...GatsbyContentfulFluid_withWebp
          }
        }
        imageDesktop {
          fluid(maxWidth: 1440) {
            ...GatsbyContentfulFluid_withWebp
          }
        }
        text {
          json
        }
        button {
          text
        }
        subscribeForm {
          klaviyoListId
          successMessage {
            json
          }
        }
        product {
          ...ContentfulProduct
        }
      }
    }
  }
`
